.header_outer_container div {
  display: flex;
  background-color: black;
  opacity: 0.8;
  color: white;
  align-items: center;
  /* justify-content: pace-between; */
  /* margin-top: -1vh; */
}

.header_outer_container{
  z-index: -1;
  position: sticky;
  padding: 8px 20px;
}

.header_container {
  display: flex;
  z-index: 1;
  justify-content: space-between;
  position:sticky;

}

.logo p {
  color: red;
  z-index: 1;
  opacity: 1;
  font-size: 40px;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
}

.headr_right ul {
  display: flex;
  gap: 15px;
  justify-content: space-around;
  list-style: none;
}

.header-left ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  gap: 30px;
  text-align: center;
}
